/* stylelint-disable scss/dollar-variable-empty-line-before */
/* stylelint-disable-next-line value-keyword-case */
/** Bulma: Variables **/
/** Main **/
body {
  animation: none;
}

.menu-label {
  color: hsl(0deg, 0%, 71%);
}

.menu-list a {
  color: hsl(208deg, 79%, 51%);
  border: 1px solid transparent;
}
.menu-list a.is-active {
  color: hsl(0deg, 0%, 100%);
  background: hsl(208deg, 79%, 51%);
  border-color: hsl(208deg, 79%, 51%);
}
.menu-list a:not(.is-active):hover {
  color: hsl(208deg, 79%, 51%);
  background: none;
  border-color: hsl(208deg, 79%, 51%);
}
.menu-list a[disabled] {
  color: hsl(0deg, 0%, 48%);
  pointer-events: none;
}
.menu-list a.is-loading::after {
  animation: spinAround 0.5s infinite linear;
  border: 2px solid hsl(0deg, 0%, 96%);
  border-radius: 290486px;
  border-right-color: transparent;
  border-top-color: transparent;
  content: "";
  display: block;
  height: 1em;
  width: 1em;
  right: calc(0% + 0.5em);
  top: calc(50% - 0.5em);
  position: absolute !important;
}
.menu-list a.is-active[data-action=collapse] {
  color: hsl(208deg, 79%, 51%) !important;
  background: none !important;
  border-color: transparent !important;
}
.menu-list a.is-active[data-action=collapse]:hover {
  border-color: hsl(208deg, 79%, 51%) !important;
}
.menu-list li ul {
  border-left: 0;
  padding-left: 0;
}
.menu-list li ul li {
  border-left: 2px solid hsl(0deg, 0%, 71%);
  padding-left: 0.75em;
}

.pagination {
  margin-bottom: 1.25rem;
}

.pagination-previous:not([disabled]),
.pagination-next:not([disabled]),
.pagination-link:not([disabled]) {
  background-color: hsl(0deg, 0%, 0%);
}
.pagination-previous:not([disabled]):hover,
.pagination-next:not([disabled]):hover,
.pagination-link:not([disabled]):hover {
  background-color: hsl(0deg, 0%, 96%);
}
.pagination-previous[disabled],
.pagination-next[disabled],
.pagination-link[disabled] {
  opacity: 1;
}

.pagination-link.is-current {
  color: hsl(0deg, 0%, 0%);
  background-color: hsl(0deg, 0%, 96%);
  border-color: hsl(0deg, 0%, 96%);
}
.pagination-link.is-current.is-loading::after {
  border-bottom-color: hsl(0deg, 0%, 29%);
  border-left-color: hsl(0deg, 0%, 29%);
}

li[data-action=page-ellipsis] {
  cursor: pointer;
}

.label {
  color: hsl(0deg, 0%, 93%);
}

#page {
  /* fix overflow issue with flex */
  min-width: 0;
}

.is-linethrough {
  text-decoration: line-through;
}

#dashboard.is-loading *[data-action],
#dashboard.is-loading #menu .menu-list a {
  cursor: progress;
}
#dashboard.is-loading *[data-action$=-help],
#dashboard.is-loading .table *[data-action],
#dashboard.is-loading .column.bulk-operations *[data-action],
#dashboard.is-loading .column.exclusive-operations *[data-action] {
  cursor: pointer;
}

.statistics thead > tr > :first-child,
.statistics tbody > tr > :first-child {
  min-width: 33.3%;
}
.statistics thead > tr > :nth-child(2) :first-child,
.statistics tbody > tr > :nth-child(2) :first-child {
  min-width: 50%;
}

.expirydate {
  color: hsl(0deg, 0%, 93%);
}

.table .originalname {
  max-width: 200px;
  text-overflow: ellipsis;
  overflow: hidden;
}
.table th[title],
.table td[title] {
  cursor: help;
}
.table th[data-action],
.table td[data-action] {
  cursor: pointer;
}
.table th[data-action]:hover,
.table td[data-action]:hover {
  text-decoration: dotted underline;
}
.table th i[data-action],
.table td i[data-action] {
  cursor: pointer;
}

/** Thumbs extension **/
.image-container .checkbox {
  position: absolute;
  top: 11px;
  left: 11px;
}
.image-container .controls {
  display: flex;
  position: absolute;
  top: 11px;
  right: 11px;
}
.image-container .controls .button {
  border-radius: 0;
}
.image-container .controls .button:not(:active, :hover) {
  color: hsl(0deg, 0%, 100%);
  background-color: rgba(0, 0, 0, 0.56);
}

.no-touch .image-container .checkbox {
  opacity: 0.5;
}
.no-touch .image-container .controls,
.no-touch .image-container .details {
  opacity: 0;
}
.no-touch .image-container:hover .checkbox,
.no-touch .image-container:hover .controls,
.no-touch .image-container:hover .details {
  opacity: 1;
}